.forgotPasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;  
    left: 0;
  }
  
  .forgotPasswordBox {
    background-color: white;
    width: 25vw;
    height: 45vh;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 25px;
  }

  .forgotPasswordHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
  }
  
  .forgotPasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 1vmax 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
  }
 
  .forgotPasswordForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  .forgotPasswordForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 40px;
    font: 300 0.9vmax cursive;
    outline: none;
  }
  
  .forgotPasswordForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
  }

  .forgotPasswordBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  }
  
  .forgotPasswordBtn:hover {
    background-color: rgb(179, 66, 46);
  }
  
  @media screen and (max-width: 800px) {
    .forgotPasswordContainer {
      /* background-color: white; */
      background-color: rgb(231, 231, 231);
    }
    .forgotPasswordBox {
      width: 50vw;
      height: 50vh;
    }

    .forgotPasswordHeading {
        font: 400 2.3vmax "Roboto";
        padding: 3vmax 1.3vmax;
      }
      

    .forgotPasswordForm {
      padding: 2vmax 6vmax;
    }
  
    .forgotPasswordForm > div > input {
      padding: 1.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .forgotPasswordForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .forgotPasswordForm > a {
      font: 500 1.8vmax "Gill Sans";
    }
  
    .forgotPasswordBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }

  @media screen and (max-width: 600px) {
    .forgotPasswordContainer {
      /* background-color: white; */
      background-color: rgb(231, 231, 231);
    }
    .forgotPasswordBox {
      width: 75vw;
      height: 40vh;
    }

    .forgotPasswordHeading {
        font: 400 2.3vmax "Roboto";
        padding: 3vmax 1.3vmax;
      }
      
  
    .forgotPasswordForm {
      padding: 2vmax 4vmax;
    }
  
    .forgotPasswordForm > div > input {
      padding: 1.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .forgotPasswordForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .forgotPasswordForm > a {
      font: 500 1.8vmax "Gill Sans";
    }
  
    .forgotPasswordBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }